* {
  box-sizing: border-box;
  margin: 0;
}

.container {
  max-width: 1320px;
  min-width: 375px;
  margin: 0 auto;
  padding: 0 15px;
}

a {
  text-decoration: none;
}

.body {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-v25-latin-regular.woff2") format("woff2"), url("../fonts/montserrat-v25-latin-regular.woff") format("woff");
}

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-v25-latin-700.woff2") format("woff2"), url("../fonts/montserrat-v25-latin-700.woff") format("woff");
}

.background {
  background: url("../img/h.svg") no-repeat center top, linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  background-size: 523px 600px, cover;
  border-radius: 0 0 12px 12px;
}

.header__section {
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: min-content min-content;
  position: relative;
}

.header__info {
  grid-column: span 2;
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
}

.header__info-list {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
}

.header__info-list-link {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  color: #B3B3BA;
  width: 33px;
  height: 25px;
}

.header__info-list-link:hover {
  width: 33px;
  height: 25px;
  background-color: #F5F5FA;
  border-radius: 8px;
}

.header__info-list_active {
  background-color: #F0145A;
  border-radius: 8px;
  width: 33px;
  height: 25px;
  color: #FFFFFF;
}

.header__info-list_active:hover {
  color: #B3B3BA;
}

.header__info-adress {
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #B3B3BA;
}

.header__info-phone {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #B3B3BA;
  position: relative;
  z-index: 2;
}

.header__info-phone:hover {
  color: #F0145A;
}

.header__info-fb {
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center;
}

.header__info-fb:hover path {
  fill: #F0145A;
}

.header__info::after {
  content: '';
  background-color: rgba(245, 245, 250, 0.2);
  height: 45px;
  width: 1000px;
  position: absolute;
  top: 0;
  left: -100px;
  z-index: 1;
}

.header__logo {
  display: flex;
  padding-top: 15px;
  padding-bottom: 10px;
}

.header__logo-item {
  display: flex;
  background-image: url("../img/logo-m.svg");
  background-size: cover;
  width: 70px;
  height: 32px;
  background-repeat: no-repeat;
  background-position: center;
}

.header__logo-item:hover {
  opacity: 0.5;
}

.header__menu {
  display: flex;
  align-items: center;
}

.header__menu-btn {
  cursor: pointer;
  background-image: url("../img/menu-btn-1.svg");
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  background-color: transparent;
}

.header__menu-btn:hover {
  background-color: #E6E6F0;
  border-radius: 8px;
}

.header__menu-link {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #24A3FF;
}

.header__menu-link:hover {
  border-bottom: 2px solid #24A3FF;
}

.header__menu_active {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #F0145A;
  border-bottom: 2px solid #F0145A;
}

.header__menu_active:hover {
  color: #24A3FF;
}

.top {
  padding-top: 138px;
  padding-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.top__txt {
  font-weight: 700;
  font-size: 38px;
  line-height: 46px;
  text-align: center;
  color: #FFFFFF;
  width: 315px;
  margin-bottom: 159px;
}

.top__scroll {
  display: block;
  background-image: url("../img/scroll.svg");
  width: 42px;
  height: 42px;
  background-repeat: no-repeat;
  background-position: center;
  border: 2px solid rgba(245, 245, 250, 0.5);
  border-radius: 50%;
}

.header__info-adress,
.header__info-fb,
.header__menu-nav {
  display: none;
}

@media (min-width: 768px) {
  .background {
    background-size: 894px 1024px, cover;
  }
  .header__section {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, min-content);
  }
  .header__info {
    align-items: center;
    justify-content: flex-start;
    grid-column: 1 / -1;
  }
  .header__info-data {
    display: flex;
    align-items: center;
    position: relative;
  }
  .header__info-adress {
    display: block;
    margin-left: 18px;
  }
  .header__info-adress::after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
    position: absolute;
    left: 7px;
    top: 8px;
  }
  .header__info-phone {
    margin-left: 18px;
  }
  .header__info-phone::after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
    position: absolute;
    left: 224px;
    top: 8px;
  }
  .header__info-fb {
    display: block;
    margin-left: 18px;
  }
  .header__info-fb::after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
    position: absolute;
    left: 345px;
    top: 8px;
  }
  .header__info::after {
    display: none;
  }
  .header__logo {
    padding-top: 20px;
    padding-bottom: 30px;
  }
  .header__logo-item {
    width: 130px;
    height: 60px;
  }
  .header__menu-btn {
    display: none;
  }
  .header__menu-nav {
    display: flex;
  }
  .header__menu-link {
    margin-right: 15px;
  }
}

@media (min-width: 992px) {
  .background {
    background-size: 670px 768px, cover;
  }
  .header__section {
    grid-template-columns: min-content 1fr;
    grid-template-rows: repeat(2, min-content);
  }
  .header__info {
    grid-row: 1 / -1;
    grid-column: 2 / 3;
    justify-content: flex-end;
    padding-top: 0;
    padding-bottom: 18px;
  }
  .header__logo {
    padding-top: 50px;
    grid-row: 1 / -1;
  }
  .header__menu {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
    justify-self: end;
    padding-bottom: 38px;
  }
}

.footer__section {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, min-content);
  justify-items: center;
  padding-top: 32px;
  padding-bottom: 30px;
}

.footer__adress {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 21px;
  grid-row: 1 / 2;
}

.footer__adress-itm {
  font-size: 16px;
  line-height: 20px;
  color: #323264;
}

.footer__adress-itm:hover {
  opacity: 0.5;
}

.footer__adress-lnk {
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #24A3FF;
  margin-top: 10px;
}

.footer__adress-lnk:hover {
  color: #F0145A;
}

.footer__contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row: 2 / 3;
}

.footer__contacts-icon path {
  fill: #24A3FF;
}

.footer__contacts-icon path:hover {
  fill: #F0145A;
}

.footer__contacts-tel {
  cursor: pointer;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #46466E;
  margin-top: 10px;
}

.footer__contacts-tel:hover {
  opacity: 0.5;
}

.footer__contacts-mail {
  cursor: pointer;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #24A3FF;
  margin-top: 10px;
}

.footer__contacts-mail:hover {
  color: #F0145A;
}

.footer__copy {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row: 3 / 4;
  margin-top: 30px;
}

.footer__copy-txt {
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #B3B3BA;
}

@media (min-width: 768px) {
  .footer__section {
    width: 400px;
    margin: 0 auto;
    justify-items: start;
  }
  .footer__adress {
    flex-direction: row;
    justify-content: flex-start;
    position: relative;
  }
  .footer__adress-lnk {
    margin-top: 0;
    margin-left: 26px;
  }
  .footer__adress-lnk::after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
    position: absolute;
    left: 210px;
    top: 8px;
  }
  .footer__contacts {
    flex-direction: row;
    align-items: center;
    position: relative;
  }
  .footer__contacts-icon {
    margin-right: 26px;
  }
  .footer__contacts-tel {
    margin-right: 26px;
    margin-top: 0;
  }
  .footer__contacts-tel::after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
    position: absolute;
    left: 35px;
    top: 12px;
  }
  .footer__contacts-mail {
    margin-top: 0;
  }
  .footer__contacts-mail::after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #E6E6F0;
    border-radius: 50%;
    position: absolute;
    left: 164px;
    top: 12px;
  }
  .footer__copy {
    flex-direction: row;
    width: 400px;
    justify-content: center;
  }
}

@media (min-width: 992px) {
  .footer__section {
    grid-template-columns: 1fr min-content;
    grid-template-rows: repeat(2, min-content);
    width: auto;
  }
  .footer__adress {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
  }
  .footer__contacts {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
  }
  .footer__copy {
    grid-row: 1 / 3;
    grid-column: 2 / 3;
    margin-top: 0;
    align-items: flex-start;
  }
}
